  keyIdStringGoiDichVuKhamSucKhoeVo: KeyIdStringGoiDichVuKhamSucKhoeVo = new KeyIdStringGoiDichVuKhamSucKhoeVo();
  dichVuStringId: string = null;
  icClose = icClose;
  icDelete = icDelete;
  isSelectDuocPham: boolean = false;
